import React, { useState } from "react";
import { Grid, TextField, Button } from "@material-ui/core";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

// styles
import useStyles from "./styles";

// components
import PageTitle from "../../components/PageTitle";
import Widget from "../../components/Widget";
import { Typography } from "../../components/Wrappers";
import OtherRadio from "./OtherRadio";

export default function RequestPage() {
  var classes = useStyles();
  const [pmemail, setPmemail] = React.useState('');
  const [pmode, setPmode] = React.useState('');

  const handleChange = event => {
    setPmemail(event.target.value);
  };
  const handleChange1 = event => {
    setPmode(event.target.value);
  };
  
  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, []);
  const inputLabel1 = React.useRef(null);
  const [labelWidth1, setLabelWidth1] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth1(inputLabel1.current.offsetWidth);
  }, []);
  
  const [value, setValue] = useState('');
  const [otherValue, setOtherValue] = useState('');
  
  const [selectedFile, setSelectedFile] = useState(null);

  const handleChange2 = event => {
	setValue(event.target.value)
  };
  const handleOthers = event => {
	setOtherValue(event.target.value)
	setValue(event.target.value)
  };
  
  const uploadHandle = event => {
	console.log(event.target.files);
	console.log(event.target.files[0])
	setSelectedFile(event.target.files)
  };
  
  const handleSubmit = e => {
    e.preventDefault();
	const data = new FormData(e.target);
	data.append('file', selectedFile)
    console.log(value);
	console.log(data.get("site-name"));
	console.log(data.get("name"));
	console.log(data.get("email"));
	console.log(pmemail);
	console.log(data.get("file"));
	console.log(data.get("amt-req"));	
	console.log(pmode);
	console.log(data.get("rsn-req"));
  };

  return (
    <>
      <PageTitle title="Request Form" />
      <Grid container spacing={4}>
          <Widget disableWidgetMenu>
			<form onSubmit={handleSubmit} className={classes.root} autoComplete="off">
			    <Grid container spacing={2}>
			    <Grid item xs={12} sm={4}>
			    <FormControl component="fieldset">
			    <FormLabel component="legend">Submission Type</FormLabel>
			    <RadioGroup required aria-label="Submission Type" name="subtype" value={value} onChange={handleChange2}>
			    <FormControlLabel value="subtype1" name="subtype1" control={<Radio />} label="New Request" />
			    <FormControlLabel value="subtype2" name="subtype2" control={<Radio />} label="Approval by Project Manager"/>
			    <FormControlLabel value="subtype3" name="subtype3" control={<Radio />} label="Approval by Regional Head"/>
				<OtherRadio onTextChange={handleOthers} value={otherValue} placeholder="Other"/>
			    </RadioGroup>
			    </FormControl>
				</Grid>
				<Grid item xs={12} sm={8}>
				<Grid container spacing={2}>
				<Grid item xs={12} sm={6}><TextField required fullWidth id="site-name" name="site-name" label="Site Name" variant="outlined"/></Grid>
				<Grid item xs={12} sm={6}><TextField required fullWidth id="name" name="name" label="Name" variant="outlined" /></Grid>
				<Grid item xs={12} sm={6}><TextField required fullWidth id="email" name="email" type="email" label="Email Id" variant="outlined" /></Grid>
				<Grid item xs={12} sm={6}><FormControl variant="outlined" fullWidth className={classes.formControl}>
				<InputLabel required ref={inputLabel} id="pmemail-ip">Project Manager Email</InputLabel>
				<Select labelId="pmemail-slctlbl" id="pmemail-slct" value={pmemail} onChange={handleChange} labelWidth={labelWidth}>
				<MenuItem value={"pmemail1@gmail.com"}>pmemail1@gmail.com</MenuItem>
				<MenuItem value={"pmemail2@gmail.com"}>pmemail2@gmail.com</MenuItem>
				<MenuItem value={"pmemail3@gmail.com"}>pmemail3@gmail.com</MenuItem>
				</Select>
				</FormControl></Grid>
				<Grid item xs={12} sm={6}>
				<Button fullWidth id="uploadbtn" name="uploadbtn" variant="outlined" color="secondary" component="label" className={classes.button} startIcon={<CloudUploadIcon />} style={{height:100+'%'}}>
				Upload Material Required
				<input
					type="file"
					name="file"
					multiple
					style={{ display: "none" }}
					onChange={uploadHandle}
				/>
				</Button>
				</Grid>
				<Grid item xs={12} sm={6}><TextField required fullWidth id="amt-req" name="amt-req" label="Amount Requested" variant="outlined" /></Grid>
			    </Grid></Grid>
				<Grid item xs={12} sm={4}><FormControl variant="outlined" fullWidth className={classes.formControl}>
				<InputLabel required ref={inputLabel1} id="pymnt-ip">Mode Of Payment</InputLabel>
				<Select	labelId="pymnt-slctlbl" id="pymnt-slct" value={pmode} onChange={handleChange1} labelWidth={labelWidth1}>
				<MenuItem value={"Cash"}>Cash</MenuItem>
				<MenuItem value={"Card"}>Card</MenuItem>
				<MenuItem value={"Cheque"}>Cheque</MenuItem>
				</Select>
				</FormControl></Grid>
				<Grid item xs={12} sm={4}><TextField required fullWidth id="rsn-req" name="rsn-req" label="Reason for Request" variant="outlined" /></Grid>
				<Grid item xs={12} sm={4}><Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit} style={{height:100+'%'}}>Submit</Button></Grid>
			  </Grid>
			</form>  
          </Widget>
      </Grid>
    </>
  );
}
